<template>
  <div class="filter-box">
    <div class="filter" v-for="(filter,filterTitle,idx1) in filters" :key="filterTitle">
      <h3 class="title">{{filterTitle}}</h3>
      <div class="checkbox">
        <input
          type="checkbox"
          :id="idx1"
          :indeterminate="filter.isIndeterminate"
          v-model="filter.isCheckAll"
          @change="checkAll(filter.isCheckAll,filterTitle)"
        />
        <label :for="idx1">全部</label>
      </div>
      <div class="checkbox-group">
        <div class="checkbox" v-for="(option,idx2) in filter.options" :key="option">
          <input
            type="checkbox"
            :id="idx2"
            :value="option"
            v-model="filter.checkedFilters"
            @change="checkFilters(filter.checkedFilters,filterTitle)"
          />
          <label :for="idx2">{{option}}</label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const filters = {
  购买方式: {
    options: ['立即购买', '出价'],
    isCheckAll: false,
    checkedFilters: [],
    isIndeterminate: true
  },
  类型: {
    options: ['民谣', '音疗'],
    isCheckAll: false,
    checkedFilters: [],
    isIndeterminate: true
  },
  价格: {
    options: ['0-10BNB', '10-50BNB', '50-100BNB', '100+10BNB'],
    isCheckAll: false,
    checkedFilters: [],
    isIndeterminate: true
  }
}
export default {
  name: 'Filter',
  data () {
    return {
      filters
    }
  },
  methods: {
    checkAll (checked, title) {
      const filters = this.filters[title]
      filters.checkedFilters = checked ? filters.options : []
      filters.isIndeterminate = false
    },
    checkFilters (checked, title) {
      const checkedCount = checked.length
      const filter = this.filters[title]
      filter.isCheckAll = checkedCount === filter.options.length
      filter.isIndeterminate = checkedCount > 0 && checkedCount < filter.options.length
    }
  }
}
</script>
<style scoped>
.filter-box,
.filter,
.checkbox-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.filter {
  color: #fff;
  border-bottom: 1px solid #ccc;
  margin: 40px 0;
}
.filter .title {
  font-size: 20px;
}
.filter label {
  margin-left: 10px;
}
</style>
