<template>
  <div class="music-box">
    <Header />
    <h3 class="category-title">全部类别</h3>
    <div class="category-box">
      <span class="iconfont icon-left-move" @click="leftMove" v-if="idx > 0"></span>
      <div
        v-for="category in categorys"
        :key="category"
        class="category"
        :style="`transform: translateX(${move}vw);`"
      >
        <img :src="category.img" :alt="category.title" />
        <span>{{ category.title }}</span>
      </div>
      <span class="iconfont icon-right-move" @click="rightMove"></span>
    </div>
    <div class="content-box">
      <Filter class="filter-box" />
      <div class="filter-result-box">
        <div class="sort">
          <h3>排序：</h3>
          <select v-model="selected">
            <option>时间</option>
            <option>价格升序</option>
            <option>价格降序</option>
          </select>
        </div>
        <div class="filter-result">
          <div class="result" v-for="(item,idx) in goods" :key="item">
            <img :src="item.img" :alt="item.title" />
            <div class="desc">
              <span class="title">商品{{idx}}</span>
              <span class="price">价格：{{item.price}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>
<script>
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'
import Filter from '@/components/Filter.vue'
export default {
  name: 'Music',
  components: {
    Header,
    Footer,
    Filter
  },
  data () {
    return {
      selected: '时间',
      move: 0,
      idx: 0,
      categorys: [
        {
          img: require('../assets/4.jpg'),
          title: '京剧'
        },
        {
          img: require('../assets/5.jpg'),
          title: '相声'
        },
        {
          img: require('../assets/6.jpg'),
          title: '音乐'
        },
        {
          img: require('../assets/7.jpg'),
          title: '中国武术'
        },
        {
          img: require('../assets/8.jpg'),
          title: '河北梆子'
        },
        {
          img: require('../assets/9.jpg'),
          title: '评剧'
        }
      ],
      goods: [{
        img: require('../assets/10.jpg'),
        title: '商品',
        price: 10
      }, {
        img: require('../assets/11.jpg'),
        title: '商品',
        price: 10
      }, {
        img: require('../assets/12.jpg'),
        title: '商品',
        price: 10
      }, {
        img: require('../assets/13.jpg'),
        title: '商品',
        price: 10
      }, {
        img: require('../assets/14.jpg'),
        title: '商品',
        price: 10
      }, {
        img: require('../assets/15.jpg'),
        title: '商品',
        price: 10
      }]

    }
  },
  methods: {
    leftMove () {
      console.log(this.idx)
      if (this.idx > 0) {
        this.move += 22.5
        this.idx--
      }
    },
    rightMove () {
      console.log(this.idx)
      if (this.idx + 3 === this.categorys.length - 1) {
        this.move += 22.5 * this.idx
        this.idx = -1
      } else {
        this.move -= 22.5
      }
      this.idx++
    }
  }
}
</script>
